@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');
@keyframes fadein {
    from {
        opacity:0;
     
    }
    to {
        opacity:1;
      
    }
}

nav {
    display: flex;
    z-index: 99 !important;
    position: fixed;
    justify-content: center;    
    background-color: rgba(62, 62, 177, 0);
    opacity: 0;
    animation: fadein 2s 4s;
    animation-fill-mode: forwards;
}

.navBarContainer {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.navBarUl {
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.navLi {
    margin: 0;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(238, 238, 238);
    padding: 1vw;
    letter-spacing: 1px;
    list-style: none;
    text-decoration: none;
    font-family: 'Oxygen', sans-serif;
    transition: All 0.2s ease-in-out;
    cursor: pointer;
    border: 1px solid rgba(238, 130, 238, 0);
   
}

.navLi:hover {
    color: #d24141;
}

@media (max-width: 500px) {

    .navBarContainer {
        max-width: 100vw;
    }
    .navLi {
        font-size: 1.2rem;
     
    }
    .navBarUl {
        gap: 10px;
    }
}